.checkBoxOpcii:checked + .textCheckboxOpcii::before {
  border-color: #664EF9;
  background-color: #664EF9;
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.5 5.41478L4.95177 9.87505L13.5 1.33527L12.2728 0.125L4.95177 7.43749L1.71024 4.19599L0.5 5.41478Z' fill='white'/%3E%3C/svg%3E%0A");
}

.checkBoxOpcii:not(:disabled):not(:checked)+.textCheckboxOpcii:hover::before {
  background: rgba(102, 78, 249, 0.1);
  border: 1px solid rgba(102, 78, 249, 0.1);
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.5 5.41478L4.95177 9.87505L13.5 1.33527L12.2728 0.125L4.95177 7.43749L1.71024 4.19599L0.5 5.41478Z' fill='white'/%3E%3C/svg%3E%0A");
}

.checkBoxOpcii:not(:disabled):active+.textCheckboxOpcii::before {
  background-color: #664EF9;
  border-color: #664EF9;
}

.checkBoxOpcii:focus+.textCheckboxOpcii::before {
  box-shadow: 0 0 10px 5px rgba(102, 78, 249, .25);
}


